import { useSelector } from "react-redux";

export const Skills = () => {
    const theme = useSelector(state => state.theme);

    const skills = [
        {
            name: "Hard skills",
            list: ["HTML, CSS, SCSS", " ", "Java Script", "React", "Git/GitHub", "Node.js", "  -  "],
        },
        {
            name: "Soft skills",
            list: ["  ,    ", "      ", " ,      ,   ", "      ,   c ", "    "],
        }
    ]

    return (
        <div className="skills">
            <div id='skillsBox' className={`skills-box ${theme === 'light' ? 'dark' : 'light'}`}>
                <h1 className="skills-title"></h1>
                {skills.map((skill, index) => (
                    <details key={index} className="skills-details">
                        <summary className="skills-subtitle">{skill.name}</summary>
                        <ul className={`skills-list ${theme}`}>
                            {skill.list.map((item, i) => (
                                <li key={i}>{item}</li>
                            ))}
                        </ul>
                    </details>
                ))}                
            </div>
        </div>
    )
}
